import {
  Row,
  Tab,
  Col,
  Nav,
  Button,
  Dropdown,
  InputGroup,
  FormControl,
  Form,
  Spinner,
} from "react-bootstrap";
import React from "react";
import {
  queryCollectionByTradeCountScaled,
  queryCollectionByTradeVolume,
  queryNftByNewest,
  queryNft,
  queryNftBySearch,
  queryCollectionBySearch,
} from "./api";
import { useLocation } from "react-router-dom";
import CollectionCard from "./CollectionCard";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBolt,
  faFire,
  faList,
  faSearch,
  faArrowRight,
} from "@fortawesome/free-solid-svg-icons";
import NFTCard from "./NFTCard";

function ExploreController() {
  const location = useLocation();
  let view = location.pathname.split("/").pop();
  view = ["top", "all", "search"].includes(view) ? view : "trending";

  // For search view.
  const [search, setSearch] = React.useState("");
  const [searchSubView, setSearchSubView] = React.useState(null);
  const [searchCollections, setSearchCollections] = React.useState(null);
  const [searchNfts, setSearchNfts] = React.useState(null);

  // For explore view.
  const [collectionList, setCollectionList] = React.useState(null);
  const [nftList, setNftList] = React.useState(null);
  const [nftListEnd, setNftListEnd] = React.useState(false);
  const [queryText, setQueryText] = React.useState("Newly listed");

  const [viewMoreEnabled, setViewMoreEnabled] = React.useState(true);

  React.useEffect(() => {
    document.title = `Explore`;
    (async () => {
      if (view === "trending") {
        const l = await queryCollectionByTradeCountScaled();
        setCollectionList(l.results);
      } else if (view === "top") {
        const l = await queryCollectionByTradeVolume();
        setCollectionList(l.results);
      } else if (view === "all") {
        const l = (await queryNftByNewest({ beforeId: null })).results;
        if (l.length < 100) {
          setNftListEnd(true);
        }
        setNftList(l);
      } else if (view === "search") {
        setSearch(new URLSearchParams(location.search).get("query"));
        await doChangeSearchSubview("collections");
      }
    })();
  }, []);

  const doSearch = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();

    if (search.trim() !== "") {
      window.location.assign(`/explore/search?query=${search.trim()}`);
    }
  };

  const doChangeSearchSubview = async (subview) => {
    // TODO: Figure out a more elegant way to retrieve this from the existing react state.
    const search = new URLSearchParams(location.search).get("query");

    setSearchSubView(subview);

    if (subview === "collections" && searchCollections === null) {
      setSearchCollections(
        (await queryCollectionBySearch({ search: search })).results
      );
    }
    if (subview === "nfts" && searchNfts === null) {
      setSearchNfts((await queryNftBySearch({ search: search })).results);
    }
  };

  const doChangeQuery = async (eventKey, event) => {
    const [key, direction] = eventKey.split("|");
    setQueryText(event.target.innerText);

    setNftListEnd(false);
    if (key === "listHeight") {
      const l = (await queryNftByNewest({ beforeId: null })).results;
      if (l.length < 100) {
        setNftListEnd(true);
      }
      setNftList(l);
      setViewMoreEnabled(true);
    } else {
      const l = await queryNft({
        key: key,
        direction: direction,
        limit: 100,
        offset: 0,
      });
      setNftList(
        l.results.map((x) => {
          return {
            address: x,
          };
        })
      );
      setViewMoreEnabled(false);
    }
  };

  const onViewMore = async () => {
    const l = (
      await queryNftByNewest({ beforeId: nftList[nftList.length - 1].id })
    ).results;
    if (l.length < 100) {
      setNftListEnd(true);
    }
    setNftList(nftList.concat(l));
  };

  return (
    <div className="d-flex flex-column">
      <div
        className="mx-auto mt-4 accent text-center d-flex flex-column"
        style={{ maxWidth: "584px", width: "100%" }}
      >
        <h1>Explore</h1>
        <Form onSubmit={doSearch}>
          <InputGroup className="mt-4">
            <InputGroup.Text id="basic-addon1">
              <FontAwesomeIcon icon={faSearch} />
            </InputGroup.Text>
            {search !== null && (
              <FormControl
                className="search-box"
                placeholder={"Search all collections and NFTs..."}
                value={search}
                onChange={(e) => {
                  if (e.target.value.length <= 50) {
                    setSearch(e.target.value);
                  }
                }}
              />
            )}
            {search !== "" && (
              <Button variant="primary" type="submit">
                <FontAwesomeIcon icon={faArrowRight} />
              </Button>
            )}
          </InputGroup>
        </Form>
      </div>
      {view === "search" && (
        <Tab.Container
          id="left-tabs-example"
          activeKey={searchSubView}
          onSelect={doChangeSearchSubview}
        >
          <Row>
            <Nav variant="tabs" className="custom-nav-tabs mt-4">
              <Nav.Item className="ms-auto">
                <Nav.Link eventKey="collections">
                  <FontAwesomeIcon icon={faBolt} className="me-2" />
                  Collections
                </Nav.Link>
              </Nav.Item>
              <Nav.Item className="me-auto">
                <Nav.Link eventKey="nfts">
                  <FontAwesomeIcon icon={faList} className="me-2" />
                  NFTs
                </Nav.Link>
              </Nav.Item>
            </Nav>
            <hr className="light-border" />
          </Row>
          <Row>
            <Tab.Content>
              <Tab.Pane eventKey="collections">
                <ul className="collection-cards-container mt-4">
                  {searchCollections === null && (
                    <div className="d-flex flex-row">
                      <Spinner className="mx-auto" animation="border" />
                    </div>
                  )}
                  {searchCollections !== null &&
                    searchCollections.map((x) => (
                      <a key={x} href={`/collection/${x}`}>
                        <CollectionCard handle={x} />
                      </a>
                    ))}
                  {searchCollections !== null && searchCollections.length === 0 && (
                    <div className="d-flex flex-row">
                      <p className="mx-auto text-muted" animation="border">No matching collection</p>
                    </div>
                  )}
                </ul>
              </Tab.Pane>
              <Tab.Pane eventKey="nfts">
                <ul className="cards-container mt-4">
                  {searchNfts === null && (
                    <div className="d-flex flex-row">
                      <Spinner className="mx-auto" animation="border" />
                    </div>
                  )}
                  {searchNfts !== null &&
                    searchNfts.map((x) => (
                      <a key={x.address} href={`/nft/${x}`}>
                        <NFTCard address={x} />
                      </a>
                    ))}
                  {searchNfts !== null && searchNfts.length === 0 && (
                    <div className="d-flex flex-row">
                      <p className="mx-auto text-muted" animation="border">No matching NFTs</p>
                    </div>
                  )}
                </ul>
              </Tab.Pane>
            </Tab.Content>
          </Row>
        </Tab.Container>
      )}
      {(view === "trending" || view === "top" || view === "all") && (
        <Tab.Container id="left-tabs-example" defaultActiveKey="first">
          <Row>
            <Nav variant="tabs" className="custom-nav-tabs mt-4">
              <Nav.Item className="ms-auto">
                <Nav.Link href="/explore/" active={view === "trending"}>
                  <FontAwesomeIcon icon={faBolt} className="me-2" />
                  Trending
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link href="/explore/top" active={view === "top"}>
                  <FontAwesomeIcon icon={faFire} className="me-2" />
                  Top
                </Nav.Link>
              </Nav.Item>
              <Nav.Item className="me-auto">
                <Nav.Link href="/explore/all" active={view === "all"}>
                  <FontAwesomeIcon icon={faList} className="me-2" />
                  All NFTs
                </Nav.Link>
              </Nav.Item>
            </Nav>
            <hr className="light-border" />
          </Row>
          <Row>
            {["trending", "top"].includes(view) && collectionList !== null && (
              <Tab.Content>
                <ul className="collection-cards-container mt-4">
                  {collectionList !== null &&
                    collectionList.map((x) => (
                      <a key={x} href={`/collection/${x}`}>
                        <CollectionCard handle={x} />
                      </a>
                    ))}
                </ul>
              </Tab.Content>
            )}
            {view === "all" && (
              <Tab.Content>
                <Dropdown
                  onSelect={doChangeQuery}
                  style={{ textAlign: "center" }}
                >
                  <Dropdown.Toggle className="px-4 mt-4">
                    {queryText}
                  </Dropdown.Toggle>
                  <Dropdown.Menu>
                    <Dropdown.Item eventKey="listHeight|DESC">
                      Newly listed
                    </Dropdown.Item>
                    <Dropdown.Item eventKey="tradeHeight|DESC">
                      Recently sold
                    </Dropdown.Item>
                    <Dropdown.Item eventKey="price|DESC">
                      Highest price
                    </Dropdown.Item>
                    <Dropdown.Item eventKey="price|ASC">
                      Lowest price
                    </Dropdown.Item>
                    <Dropdown.Item eventKey="tradeCount|DESC">
                      Highest trade count
                    </Dropdown.Item>
                    <Dropdown.Item eventKey="tradeCount|ASC">
                      Lowest trade count
                    </Dropdown.Item>
                    <Dropdown.Item eventKey="tradeVolume|DESC">
                      Highest trade volume
                    </Dropdown.Item>
                    <Dropdown.Item eventKey="tradeVolume|ASC">
                      Lowest trade volume
                    </Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
                <ul className="cards-container mt-4">
                  {nftList !== null &&
                    nftList.map((x) => (
                      <a key={x.address} href={`/nft/${x.address}`}>
                        <NFTCard address={x.address} />
                      </a>
                    ))}
                  {viewMoreEnabled && nftList !== null && !nftListEnd && (
                    <Button onClick={onViewMore}>View more</Button>
                  )}
                </ul>
              </Tab.Content>
            )}
          </Row>
        </Tab.Container>
      )}
    </div>
  );
}

export default ExploreController;
